/* Navbar.css */
.navbar {
  background-color: black;
  color: white;
  padding: 10px;
}

.nav-links {
  list-style: none;
  display: flex;
  padding-left: 0;
  align-items: center;
}

.nav-links li {
  margin-right: 60px;
}

.nav-title {
  font-family: 'Title';
  color: #0011ff!important; 
  text-decoration: none;
  font-size: 25px!important;
}

.nav-links a {
  color: white;
  text-decoration: none;
}

.nav-title li {
  margin-right: 60px;
}
body {
  font-family: 'Normal', sans-serif;
}
